import { Controller, Scene } from "react-scrollmagic";
import React, { useState,Suspense, useRef } from 'react';
import { Canvas, useLoader, useFrame } from 'react-three-fiber';
import Lights from './Three/lights'
import Floor from './Three/floor';
import {softShadows, Loader, OrbitControls } from '@react-three/drei'
import "./About.css";
import Reactjs from '../../images/React.png'
import Pythonp from '../../images/Python.png'
import HTMLp from '../../images/HTML.png'
import Dockerp from '../../images/Docker.png'
import Solidityp from '../../images/Solidity.png'
import JSp from '../../images/JS.png'
import { TextureLoader } from "three";


softShadows()

const Box = ({ position, args, color}) => {
  const [texture1,texture2,texture3, texture4,texture5,texture6] = useLoader(TextureLoader,[HTMLp, Reactjs, Pythonp, JSp, Dockerp, Solidityp ])  
  const mesh = useRef(null);
  useFrame(()=> ( mesh.current.rotation.x = mesh.current.rotation.y += 0.01));
  return (
    <mesh castShadow position={position} ref={mesh}>

      <boxBufferGeometry attach="geometry" args={args} color={color} />
      <meshBasicMaterial attachArray='material' map={texture1} />
      <meshBasicMaterial attachArray='material' map={texture2} />
      <meshBasicMaterial attachArray='material' map={texture3} />
      <meshBasicMaterial attachArray='material' map={texture4} />
      <meshBasicMaterial attachArray='material' map={texture5} />
      <meshBasicMaterial attachArray='material' map={texture6} />
    </mesh>
  );
};

const About = () => {
  const [open, setOpen] = useState(false)
  return (
    <div className="container">
      <div className="container-inner" id="Skillset">
        <div className="empty-row">
          <div className="line" />
        </div>
        <div className="content-row" id="trigger">
          <Controller>
            <div className="Left-column">
                <div className="EmptySpace">
              <Scene classToggle="typewriter" triggerElement="#trigger">
                <div>
                  <h1 className="Skills">Skills and Experiences</h1>
                </div>
              </Scene>
              </div>
              <div className="Skill-Words">
              <p>
                I am mostly experienced in using Python, HTML, CSS, Javascript
                and Reactjs
              </p>
              <p>
                I have been exposed to many different coding languages in the process xx, but am on a continued journey of learning
              </p>
              <p>
              </p>
              </div>
            </div>
            <div className="right-column">
            <Canvas
        colorManagement
        shadows
        
        >
          <Lights />
          <Suspense fallback={null}>
            <Box position={[0,1,0]} args={[2,2,2]} /> 
            <Floor />
    
          </Suspense>
    
        </Canvas>
        <Loader />
    

            </div>
          </Controller>
        </div>
      </div>
    </div>
  );
};

export default About;
